.form {
  width: 320px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}
  
.label {
  display: flex;
  flex-direction: column;
}   
  
.input{
  width: 100%;
  margin-bottom: 16px;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 3px;}     
  
.button{
  background-color: #04aa6d;
  color: white;
  padding: 12px;
  margin: 10px 0;
  border: none;
  width: 100%;
  border-radius: 3px;
  cursor: pointer;
  font-size: 17px;
  transition: background-color 250ms linear;
}
    .button:hover {
      background-color: #3f7241;
    } 
  
    